@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;600;700&display=swap");

/* BASE STYLES  */

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

html,
body,
main,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

/* Loader */

.loader-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background: #111;
}

.loader-container .loader {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
}

.loader-container .loader span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  pointer-events: none;
  animation: animateThreads 5s linear infinite;
}

.loader-container .loader span:nth-child(1) {
  border-color: #f1c40f;
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
}

.loader-container .loader span:nth-child(2) {
  border-color: #1abc9c;
  animation-direction: reverse;
  border-radius: 67% 33% 70% 30% / 75% 65% 35% 25%;
}

.loader-container .loader span:nth-child(3) {
  border-color: #3498db;
  animation-duration: 3s;
  border-radius: 52% 48% 70% 30% / 41% 65% 35% 59%;
}

@keyframes animateThreads {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader-container .loader h2 {
  color: #fff;
  font-family: "Courier New", Courier, monospace;
  font-weight: 500;
}

/* scroll animation  */

#scroll-down-animation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h2 {
  color: #fff;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 200;
  font-size: 16px;
}

.mouse {
  margin: 0 auto;
  display: block;
  border-radius: 50px;
  border: 2px solid #b7950b;
  height: 60px;
  width: 40px;
  position: relative;
}

.move {
  position: absolute;
  background-color: #b7950b;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  left: 50%;
  transform: translateX(-50%);
  animation: move 2s linear infinite;
}

@keyframes move {
  0% {
    transform: translate(-50%, 10px);
    opacity: 0;
  }
  50% {
    transform: translate(-50%, 20px);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, 40px);
    opacity: 0;
  }
}

.hide {
  display: none;
}
